import { Icon as Iconify } from "@iconify/react";
import {
    Box,
    Container,
    InputAdornment,
    ListItem,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    SvgIcon
} from "@mui/material";
import { useState } from "react";
import { useOutletContext } from "react-router";

import FormSection from "../../../../components/FormSection";
import SingleFieldDialog from "../../../../components/SingleFieldDialog";
import TextField from "../../../../components/TextField";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";


const CustomersClubSettings = () => {
    const { form } = useOutletContext();

    const { handleSubmit, getValue, setValue } = form;

    const [fieldDialogProps, setFieldDialogProps] = useState({ open: false });
    const openField = (renderContent, options = {}) => {
        return () => {
            setFieldDialogProps({
                renderContent,
                ...options,
                open: true,
            });
        };
    };

    useAfterFirstRender(() => {
        handleSubmit();
    }, [
        getValue("showcase:name"), 
        getValue("showcase:headline"), 
        getValue("showcase:tel"),
        getValue("showcase:website"),
        getValue("showcase:instagram"),
        getValue("showcase:address")
    ]);

    return (
        <>
            <Container maxWidth="lg">
                <FormSection
                    title="هویت کسب‌وکار"
                    description="مشخصات مرتبط با هویت کسب و کار که در منو دیجیتال به کاربران نمایش داده خواهد شد."
                >
                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="نام کسب‌وکار"
                                        variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "نام کسب‌و‌کار",
                                    defaultValue: getValue("showcase:name"),
                                    onSubmit: (value) => {
                                        setValue("showcase:name", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:user-id-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("showcase:name") || "-"}
                                secondary={
                                    <>
                                        <span>نام کسب‌وکار</span>
                                        {" - "}
                                        <small>
                                            نام کسب‌وکار که در منو دیجیتال نمایش
                                            داده می‌شود.
                                        </small>
                                    </>
                                }
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="شعار"
                                        variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "شعار",
                                    defaultValue: getValue("showcase:headline"),
                                    onSubmit: (value) => {
                                        setValue("showcase:headline", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:flag-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("showcase:headline") || "-"}
                                secondary={
                                    <>
                                        <span>شعار</span>
                                        {" - "}
                                        <small>
                                            متن کوتاهی که در منو زیر عنوان
                                            مجموعه نمایش داده می‌شود.
                                        </small>
                                    </>
                                }
                            />
                        </ListItemButton>
                    </ListItem>
                </FormSection>

                <FormSection
                    title="اطلاعات تماس"
                    description="اطلاعات تماس کسب‌وکار که در صفحه معرفی کسب‌وکار به کاربران نمایش داده می‌شود."
                >
                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="شماره تماس"
                                        variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "شماره تماس",
                                    defaultValue: getValue("showcase:tel"),
                                    onSubmit: (value) => {
                                        setValue("showcase:tel", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:phone-calling-rounded-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("showcase:tel") || "-"}
                                secondary={<span>شماره تماس</span>}
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="وب‌سایت"
                                        variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        InputProps={{
                                            startAdornment: <InputAdornment position="start">https://</InputAdornment>
                                        }}
                                        dir="ltr"
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "وب‌سایت",
                                    defaultValue: getValue("showcase:website"),
                                    onSubmit: (value) => {
                                        setValue("showcase:website", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:global-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("showcase:website") || "-"}
                                secondary={<span>وب‌سایت</span>}
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />
                    
                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="اینستاگرام"
                                        variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        InputProps={{
                                            startAdornment: <InputAdornment position="start">https://instagram.com/</InputAdornment>
                                        }}
                                        dir="ltr"
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "اینستاگرام",
                                    defaultValue: getValue("showcase:instagram"),
                                    onSubmit: (value) => {
                                        setValue("showcase:instagram", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="ph:instagram-logo"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("showcase:instagram") || "-"}
                                secondary={<span>اینستاگرام</span>}
                            />
                        </ListItemButton>
                    </ListItem>

                    <Box sx={{ my: 1 }} />

                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="آدرس"
                                        multiline
                                        rows={3}
                                        variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "آدرس",
                                    defaultValue: getValue("showcase:address"),
                                    onSubmit: (value) => {
                                        setValue("showcase:address", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon
                                    component={Iconify}
                                    icon="solar:map-point-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("showcase:address") || "-"}
                                secondary={<span>آدرس</span>}
                            />
                        </ListItemButton>
                    </ListItem>
                </FormSection>
            </Container>

            <SingleFieldDialog
                {...fieldDialogProps}
                onClose={() => setFieldDialogProps({ open: false })}
            />
        </>
    );
};

export default CustomersClubSettings;
